:root
{
    --bg-dark: #c9c9c9;
    --bg: #e3e3e3;
    --bg-light: #ecf0f1;
    --primary: #89bdd3;
    --primary-light: #9ad3de;
    --danger: #e74c3c;
    --success: #2ecc71;
}
.listItem {
    width: 80%;
}

.trash:hover {
    cursor: pointer;
}

.trash {
    color: var(--danger);
}

.infos {
    width: 100%;
    padding: 20px 0px;
    text-align: center;
    color: white;
    background-color: #bdc3c7;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.2)
}
.sidebar{
    width: 100%;
    height: 100vh;
    background-color: var(--bg-dark);
    padding: 0px;
    box-shadow:-3px 3px 10px rgba(0,0,0,.3);
}

.admin {
    width: 100%;
    height: 50px;
    background-color: var(--bg-dark);
    box-shadow:-3px 4px 10px rgba(0,0,0,.3);
}
.productCard {
    width: 160px;
    height: 180px;
    font-size: 0.8em;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, .3);
    margin: 10px;
}

.productCard:hover{
    cursor: pointer;
}

.productCardDisabled,
.productCardDisabled:hover{
    opacity: 0.4;
    cursor: not-allowed;
}

.productImage {
    width: 60px;
    margin: auto;
    max-height: 60px;
}
.products {
    padding: 30px;
}
.btn-success:disabled{
    background: gray;
    border-color: gray;
}

.card{
    max-width: 80%;
}

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ellipsis div {
  position: absolute;
  top: 27px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: black;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 6px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 6px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 26px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 45px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}

.btn-sidebar {
    background-color: var(--primary);
    width: 100%;
    height: 50px;
    border-radius: 0px;
    color: white;
    text-shadow:5px 5px 10px rgba(0,0,0,.3);
}

.btn-sidebar:hover {
    background-color: var(--primary-light);
    color: white;
}

.btn-sidebar:active {
    background-color: var(--primary-light);
    color: white;
}
body{
    font-family: 'Verdana', sans-serif
}

.background{
    background-color: var(--bg-light);
    height: 100vh;
}

.no-padding{
    padding: 0px;
}

img {
    display: block;
    width: 100%;
    height: auto;
    max-height: 60px;
    text-align: center;
}
